@import '../variables.scss';

.browser-image {
  width: 190px;
  height: 190px;
  background-color: var(--dark-purple);
  position: relative;
  display: flex;
  justify-content: flex-end;
  align-items: flex-end;
  box-sizing: border-box;
  box-shadow: 0px 0px 0px 0px transparent;
  transition: box-shadow $short;
}

.browser-image__overlay {
  position: absolute;
  top: 5px;
  left: 5px;
  width: 180px;
  height: 180px;
  z-index: 1;
  background-color: rgba(0, 0, 0, 0.7);
  opacity: 0;
  transition: opacity $medium;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}

.browser-image__name {
  color: var(--white);
  font-family: $primary-font-regular;
  font-size: $primary-font-size;
  margin: 20px 2px 0 2px;
  text-align: center;
}

.browser-image__is-user-favourite {
  margin: 0 15px 15px auto;
  position: relative;
  cursor: pointer;
  z-index: 2;
}

.browser-image:hover {
  .browser-image__overlay {
    opacity: 1;
  }
}

.browser-image--selected {
  box-shadow: 0px 0px 0px 4px var(--blue);
  
  .browser-image__overlay {
    top: 5px;
    left: 5px;
  }
}