@import './variables.scss';

#root {
  width: 1440px;
  height: 100vh;
}

body {
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: center;
  align-items: center;
}


// Utility classes:

.d-none {
  display: none;
}

.d-block {
  display: block;
}

.d-flex {
  display: flex;
}

.opacity-none {
  opacity: 0
}

.opacity-full {
  opacity: 1
}